___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: Arial, sans-serif;
  background-color: #000;
  color: #fff;
  text-align: center;
  margin: 0;
  padding: 0;
}

header {
  background-color: #222;
  padding: 20px 0;
}

h1 {
  margin: 0;
  font-size: 2.5rem;
  color: #FFD700;
}

main {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}
main section {
  flex: 1;
}
main section .grid {
  display: grid;
  grid-template-columns: repeat(10, 30px);
  grid-template-rows: repeat(10, 30px);
  gap: 5px;
  justify-content: center;
}
main section .grid div {
  width: 30px;
  height: 30px;
  background-color: #444;
  border: 1px solid #fff;
}

#controls {
  margin-top: 20px;
}
#controls button {
  background-color: #FFD700;
  color: #000;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  margin: 5px;
}
#controls button:hover {
  background-color: #FFA500;
}

footer {
  background-color: #222;
  padding: 10px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
